.App {
  text-align: center;
}

li{
  list-style-type: none;
}

.exit{
  width: 300px;
  height: 60px;
	background-color: #4083b2;
	border: 5px solid white;
	border-radius: 20px;
	transition: 1s;
	display: block;
	margin: auto;
}

.exit:hover{
  background-color: #15ab74;
	border-radius: 50px;
  transition: 1s;
  cursor: pointer;
}


